.main-header,
.loading {
  color: #FCDD2B
}

a {
  text-decoration: none;
}

.back-rings {
  position: fixed;
  top: -112px;
  left: 25px;
  cursor: pointer;
  animation: fade 2s ease;

  &:hover .ring {
    animation: rotateToSide .5s ease;
    animation-fill-mode: forwards;
    cursor: pointer;
  }

  &:hover h3.back-to-menu {
    filter: drop-shadow(0 0 0.75rem #00D1FF);
    cursor: pointer;
  }
}

.header-container {
  display: flex;
  justify-content: center;
  margin-top: 30px;

  h1 {
    font-size: 5rem;
    font-family: 'Jedi';
    margin: 0;
  }

  button {
    position: absolute;
    top: 40px;
    left: 40px;
  }
}

.main {
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: scroll;
  animation: fade 2s ease;
}

.card-section {
  cursor: url('../../images/crosshair.svg') 20 20, auto;
  width: 80%;
  margin: 40px auto 50px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(310px, 1fr));
  grid-gap: 45px 35px;

  &:active {
    cursor: url("../../images/crosshair_clicked.svg") 20 20, auto;
  }
}

.return-to-landing-btn {
  bottom: 10px;
  left: 10px;
  position: fixed;
  opacity: .5;
  transition: all .3s;
  width: 50px;

  &:hover {
    cursor: pointer;
    opacity: 1;
    transform: rotate(-45deg)
  }
}

h3.back-to-menu {
  position: absolute;
  top: 100px;
  left: 105px;
  transform: rotate(0deg);
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  transition: .5s ease;

  &:hover {
    filter: drop-shadow(0 0 0.75rem #00D1FF);
    cursor: pointer;
  }
}

.no-favorite-message {
  color: white;
  font-size: 2rem;
  letter-spacing: 1px;
  text-align: center;
  margin: 10px;
  font-family: 'Pathway Gothic One', sans-serif;
}

@keyframes rotateToSide {
  100% {
    transform: rotate(190deg);
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 800px) {
  .continue-to-site-btn.back-rings {
    position: fixed;
    top: -112px;
    left: 25px;
    right: unset;
  }
}

@media screen and (max-width: 675px) {
  .header-container h1 {
    font-size: 3.2rem;
    margin-top: 100px;
  }
}

@media screen and (max-width: 375px) {
  .card-section {
    margin: 32px;
  }
}

@media screen and (max-width: 350px) {
  .card-section {
    margin: 5px;
  }
}

